.uf-field-editor {
  padding-bottom: 0;
}

.uf-field-editor .grid-field__table .ss-gridfield-item {
  height: 46px;
}

.uf-field-editor .grid-field__table .ss-gridfield-item,
.uf-field-editor .grid-field__table .ss-gridfield-item:hover {
  background: #fff;
}

.uf-field-editor .grid-field__table .ss-gridfield-item td {
  border-right-width: 0;
  border-top: 1px solid #999;
}

.uf-field-editor .grid-field__table .ss-gridfield-item td:last-child {
  border-right-width: 1px;
}

.uf-field-editor .grid-field__table .ss-gridfield-item .handle {
  min-height: 46px;
}

.uf-field-editor .grid-field__table .ss-gridfield-item.flash-background {
  animation: flash-background 2s linear;
}

.uf-field-editor .grid-field__table .ss-gridfield-item.ui-sortable-placeholder {
  height: 50px;
}

.uf-field-editor .grid-field__table .ss-gridfield-item .form-group {
  padding: 0;
}

.uf-field-editor .grid-field__table .ss-gridfield-item .form-group:after {
  border-bottom: 0;
}

.uf-field-editor .grid-field__table .ss-gridfield-item .form-group .form__field-label {
  flex: auto;
  max-width: 100%;
}

.uf-field-editor .grid-field__table .ss-gridfield-item.infieldgroup,
.uf-field-editor .grid-field__table .ss-gridfield-item.infieldgroup:hover {
  background: #f2f9fd;
}

.uf-field-editor .grid-field__table .ss-gridfield-item.infieldgroup td {
  border-bottom: 0;
  border-top: 1px solid #999;
}

.uf-field-editor .grid-field__table .ss-gridfield-item.infieldgroup .col-reorder,
.uf-field-editor .grid-field__table .ss-gridfield-item.infieldgroup .handle {
  background: #bee0f8;
  border-top: 0;
}

.uf-field-editor .grid-field__table .ss-gridfield-item.infieldgroup.infieldgroup-level-2 .col-reorder,
.uf-field-editor .grid-field__table .ss-gridfield-item.infieldgroup.infieldgroup-level-2 .handle {
  background: #99cef4;
  border-top: 0;
}

.uf-field-editor .grid-field__table .ss-gridfield-item.infieldgroup.infieldgroup-level-3 .col-reorder,
.uf-field-editor .grid-field__table .ss-gridfield-item.infieldgroup.infieldgroup-level-3 .handle {
  background: #89bef4;
  border-top: 0;
}

.uf-field-editor .grid-field__table .ss-gridfield-item[data-class="SilverStripe\\UserForms\\Model\\EditableFormField\\EditableFormStep"],
.uf-field-editor .grid-field__table .ss-gridfield-item[data-class="SilverStripe\\UserForms\\Model\\EditableFormField\\EditableFormStep"]:hover {
  background: #f6f7f8;
}

.uf-field-editor .grid-field__table .ss-gridfield-item[data-class="SilverStripe\\UserForms\\Model\\EditableFormField\\EditableFormStep"] label {
  font-weight: bold;
  color: #000;
  font-size: 1.1em;
}

.uf-field-editor .grid-field__table .ss-gridfield-item[data-class="SilverStripe\\UserForms\\Model\\EditableFormField\\EditableFormStep"] td {
  border-top: 1px solid #a6b6c1;
}

.uf-field-editor .grid-field__table .ss-gridfield-item[data-class="SilverStripe\\UserForms\\Model\\EditableFormField\\EditableFormStep"] + .ss-gridfield-item td {
  border-top: 1px solid #f6f7f8;
}

.uf-field-editor .grid-field__table .ss-gridfield-item[data-class="SilverStripe\\UserForms\\Model\\EditableFormField\\EditableFormStep"] + .ss-gridfield-item[data-class="SilverStripe\\UserForms\\Model\\EditableFormField\\EditableFieldGroup"] td {
  border-top: 1px solid #a8d7f5;
}

.uf-field-editor .grid-field__table .ss-gridfield-item[data-class="SilverStripe\\UserForms\\Model\\EditableFormField\\EditableFieldGroup"] td {
  border-top: 1px solid #a8d7f5;
}

.uf-field-editor .grid-field__table .ss-gridfield-item[data-class="SilverStripe\\UserForms\\Model\\EditableFormField\\EditableFieldGroup"] .form__field-label {
  font-weight: bold;
}

.uf-field-editor .grid-field__table .ss-gridfield-item[data-class="SilverStripe\\UserForms\\Model\\EditableFormField\\EditableFieldGroupEnd"] td {
  border-bottom: 1px solid #a8d7f5;
}

.uf-field-editor .grid-field__table .ss-gridfield-item[data-class="SilverStripe\\UserForms\\Model\\EditableFormField\\EditableFieldGroupEnd"] + .ss-gridfield-item[data-class="SilverStripe\\UserForms\\Model\\EditableFormField\\EditableFieldGroupEnd"] {
  border-top: 0;
}

.uf-field-editor .grid-field__table .ss-gridfield-item[data-class="SilverStripe\\UserForms\\Model\\EditableFormField\\EditableFieldGroupEnd"] .col-buttons .action {
  display: none;
}

.uf-field-editor .sticky-buttons {
  position: fixed;
  top: 73px;
  z-index: 2;
  background: #f6f7f8;
  border-bottom: 1px solid #e5e8eb;
  padding: 12px;
  margin-left: -12px;
}

.uf-field-editor .sticky-buttons button.action {
  margin-bottom: 0;
}

.uf-field-editor .sticky-buttons ~ .ss-gridfield-table {
  margin-top: 73px;
}




